<template>
  <div class="col-12">
    <b-card class="bg-white" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>Store</strong> Range Reservations <img class="ml-3" src="@/assets/loading.gif" v-if="loading === true">
        <span class="pull-right mt-2">
          <router-link :to="{ name: 'Store Range Dashboard', params: { id: rangeID }}"><b-button type="submit" size="sm" variant="primary"><i class="fa fa-th"></i> Dashboard</b-button></router-link>
        </span>
      </div>
      <div ref="card" class="text-dark">

        <b-form-group
          label="Search Date"
          label-for="searchDate"
          :label-cols="3"
          :horizontal="true">
          <b-form-input id="searchDate" type="date" v-model="search.search_date"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.search.search_date.required">Search Date is Required</span>
        </b-form-group>

        <b-form-group
          label="Search Time"
          label-for="searchTime"
          :label-cols="3"
          :horizontal="true">
          <b-form-input id="searchTime" type="time" v-model="search.search_time"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.search.search_time.required">Search Time is Required</span>
        </b-form-group>

        <b-form-group
          label="Session Type"
          label-for="sessionType"
          :label-cols="3"
          :horizontal="true">
          <b-form-select id="sessionType"
                         :plain="true"
                         :options="options.session_types"
                         v-model="options.selected_session_type">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Session Length"
          label-for="sessionLength"
          :label-cols="3"
          :horizontal="true">
          <b-form-select id="sessionLength"
                         :plain="true"
                         :options="options.session_lengths"
                         v-model="options.selected_session_length">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Number of Lanes"
          label-for="searchLanes"
          :label-cols="3"
          :horizontal="true">
          <b-form-select id="searchLanes"
                         :plain="true"
                         :options="[
                           { value: 1, text: '1'}, { value: 2, text: '2'}, {value: 3, text: '3'}, { value: 4, text: '4'},
                           { value: 5, text: '5'}, { value: 6, text: '6'}, { value: 7, text: '7'}, { value: 8, text: '8'},
                           { value: 9, text: '9'}, { value: 10, text: '10'}, { value: 11, text: '11'}, { value: 12, text: '12'},
                           { value: 13, text: '13'}, { value: 14, text: '14'}, { value: 15, text: '15'}, { value: 16, text: '16'},
                           { value: 17, text: '17'}, { value: 18, text: '18'}, { value: 19, text: '19'}, { value: 20, text: '20'},
                           { value: 21, text: '21'}, { value: 22, text: '22'}, { value: 23, text: '23'}, { value: 24, text: '24'},
                           { value: 25, text: '25'}, { value: 26, text: '26'}, { value: 27, text: '27'}, { value: 28, text: '28'},
                           { value: 29, text: '29'}, { value: 30, text: '30'}, { value: 31, text: '31'}, { value: 32, text: '32'},
                           { value: 33, text: '33'}, { value: 34, text: '34'}, { value: 35, text: '35'}, { value: 36, text: '36'},
                           { value: 37, text: '37'}, { value: 38, text: '38'}, { value: 39, text: '39'}, { value: 40, text: '40 '},
                         ]"
                         v-model="search.search_lanes">
          </b-form-select>
          <span class="form-group__message text-danger" v-if="!$v.search.search_lanes.required">Lanes is Required</span>
        </b-form-group>

        <b-btn class="mt-3" variant="success" block @click="searchReservations()" :disabled="$v.search.$invalid || loading">Search Available</b-btn>

      </div>
    </b-card>



    <b-card v-if="searched" class="bg-white" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>Reservation</strong> Time  <img class="ml-3" src="@/assets/loading.gif" v-if="loading === true">
      </div>
      <div class="pull-left" v-if="search_time_available">
        <h4><i class="fa fa-exclamation-circle mr-2"></i>{{ this.search_time_available_time }} Available</h4>
      </div>
      <div class="pull-left" v-if="!search_time_available && !loading">
        <h5><i class="fa fa-exclamation-circle text-danger mr-2"></i>Reservation time NOT available. Please see other available times below or try another search.</h5>

        <h6 class="mt-4" v-if="blocked_times.length > 0"> <i class="fa fa-warning mr-1 text-danger"></i> Range NOT available during these times:</h6>
        <p v-for="item in blocked_times"><span v-if="item.all_day">Range Closed Today <i class="fa fa-warning ml-2 mr-2 text-danger"></i>{{ item.message }}</span><span v-if="!item.all_day">{{ item.start_time }} - {{ item.end_time}} <i class="fa fa-warning ml-2 mr-2 text-danger"></i>{{ item.message }}</span></p>

        <h6 class="mt-4">Range Hours on {{ formatDate(search.search_date) }}:</h6>
        <p v-for="item in open_times"><i class="fa fa-clock-o text-success mr-2"></i>{{ item.time_start }} - {{ item.time_stop}}</p>
        <p v-if="open_times.length < 1"><i class="fa fa-times-circle-o text-danger mr-2"></i>Range is not normally open on this day.  Please try another search.</p>
      </div>
      <div class="pull-right" v-if="search_time_available">
        <b-button type="submit" size="sm" variant="success" @click="selectAvailableTime(search_time_available_time)"><i class="fa fa-check"></i> Select Time</b-button>
      </div>
    </b-card>



    <b-card class="bg-white" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>Available</strong> Reservation Times <img class="ml-3" src="@/assets/loading.gif" v-if="loading === true">
      </div>
      <div ref="card" class="text-dark">
        <b-table striped hover :items="available_times" :fields="search_result_fields">
          <template slot="id" slot-scope="data">
            <b-button type="submit" size="sm" variant="success" @click="selectAvailableTime(data.item.time)"><i class="fa fa-check"></i> Select</b-button>
          </template>
        </b-table>
      </div>
    </b-card>





    <b-modal ref="reservationAdd" id="reservationAdd" hide-footer size="lg" centered hide-footer title="Save Customer Reservation" class="modal-dark text-light">
      <div ref="card" class="text-dark">

        <b-form-group horizontal
                      breakpoint="md"
                      label="General"
                      label-size="md"
                      label-cols="2"
                      label-class="font-weight-bold pt-0"
                      class="mb-0">

          <b-form-group
            label="Name"
            label-for="customerName"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="customerName" type="text" v-model="addReservation.client_name"></b-form-input>
            <span class="form-group__message text-danger" v-if="!$v.addReservation.client_name.required">Customer Name is Required</span>
          </b-form-group>

          <b-form-group
            label="Phone Number"
            label-for="customerPhone"
            :label-cols="3"
            :horizontal="true">
            <masked-input
              type="tel"
              name="phone"
              class="form-control"
              v-model="addReservation.phone"
              :mask="['(' , /[1-9]/, /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/]"
              :guide="true"
              placeholderChar="#" @focus="clearPhone()">
            </masked-input>
            <span class="form-group__message text-danger" v-if="!$v.addReservation.phone.required">Phone is Required</span>
          </b-form-group>

          <b-form-group
            label="Number of Lanes"
            label-for="numLanes"
            :label-cols="3"
            :horizontal="true">
            <b-form-select id="numLanes"
                           :plain="true"
                           :disabled="true"
                           :options="[
                           { value: 1, text: '1'}, { value: 2, text: '2'}, {value: 3, text: '3'}, { value: 4, text: '4'},
                           { value: 5, text: '5'}, { value: 6, text: '6'}, { value: 7, text: '7'}, { value: 8, text: '8'},
                           { value: 9, text: '9'}, { value: 10, text: '10'}, { value: 11, text: '11'}, { value: 12, text: '12'},
                           { value: 13, text: '13'}, { value: 14, text: '14'}, { value: 15, text: '15'}, { value: 16, text: '16'},
                           { value: 17, text: '17'}, { value: 18, text: '18'}, { value: 19, text: '19'}, { value: 20, text: '20'},
                           { value: 21, text: '21'}, { value: 22, text: '22'}, { value: 23, text: '23'}, { value: 24, text: '24'},
                           { value: 25, text: '25'}, { value: 26, text: '26'}, { value: 27, text: '27'}, { value: 28, text: '28'},
                           { value: 29, text: '29'}, { value: 30, text: '30'}, { value: 31, text: '31'}, { value: 32, text: '32'},
                           { value: 33, text: '33'}, { value: 34, text: '34'}, { value: 35, text: '35'}, { value: 36, text: '36'},
                           { value: 37, text: '37'}, { value: 38, text: '38'}, { value: 39, text: '39'}, { value: 40, text: '40 '},
                         ]"
                           v-model="search.search_lanes">
            </b-form-select>
          </b-form-group>

          <b-form-group
            label="Paid in Full"
            label-for="paidInFull"
            :label-cols="3"
            :horizontal="true">
            <b-form-select id="paidInFull"
                           :plain="true"
                           :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                           v-model="addReservation.paid_in_full">
            </b-form-select>
          </b-form-group>

          <b-form-group
            label="Paid Partial"
            label-for="paidPartial"
            :label-cols="3"
            :horizontal="true">
            <b-form-select id="paidPartial"
                           :plain="true"
                           :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                           v-model="addReservation.paid_partial">
            </b-form-select>
          </b-form-group>

          <b-form-group
            label="Paid Partial ($)"
            label-for="paidPartialAmount"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="paidPartialAmount" type="text" v-model="addReservation.paid_partial_amount"></b-form-input>
            <span class="form-group__message text-danger" v-if="!$v.addReservation.paid_partial_amount.required">Partial Amount is Required</span>
          </b-form-group>

        </b-form-group>

        <b-form-group horizontal
                      breakpoint="md"
                      label="Session"
                      label-size="md"
                      label-cols="2"
                      label-class="font-weight-bold pt-0"
                      class="mb-0">

          <b-form-group
            label="Session Type"
            label-for="sessionType"
            :label-cols="3"
            :horizontal="true">
            <b-form-select id="sessionType"
                           :plain="true"
                           :disabled="true"
                           :options="options.session_types"
                           v-model="options.selected_session_type">
            </b-form-select>
          </b-form-group>

          <b-form-group
            label="Session Length"
            label-for="sessionLength"
            :label-cols="3"
            :horizontal="true">
            <b-form-select id="sessionLength"
                           :plain="true"
                           :disabled="true"
                           :options="options.session_lengths"
                           v-model="options.selected_session_length">
            </b-form-select>
          </b-form-group>

          <b-form-group
            label="Reservation Date"
            label-for="reservationDate"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="reservationDate" type="text" :disabled="true" v-model="addReservation.reservation_date"></b-form-input>
          </b-form-group>

          <b-form-group
            label="Reservation Time"
            label-for="reservationTime"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="reservationTime" type="text" :disabled="true" v-model="addReservation.reservation_time"></b-form-input>
          </b-form-group>

        </b-form-group>

        <b-form-group horizontal
                      v-if="configuration.reservations.special_instructions"
                      breakpoint="md"
                      label="Instructions"
                      label-size="md"
                      label-cols="2"
                      label-class="font-weight-bold pt-0"
                      class="mb-0">

          <b-form-group
            label=""
            label-for="ackInstructions"
            :label-cols="3"
            :horizontal="true">
            <b-form-checkbox class="mt-3" id="ackInstructions" :plain="true" v-model="addReservation.special_instructions_ack">
              {{ configuration.reservations.special_instructions_text }}
            </b-form-checkbox>
          </b-form-group>

        </b-form-group>

        <b-btn class="mt-3" variant="outline-success" block @click="checkInstructions()" :disabled="$v.addReservation.$invalid">Add Reservation</b-btn>

      </div>
    </b-modal>

    <b-modal ref="ackInstructions" hide-footer title="Acknowledge Instructions" class="modal-dark text-light">
      <div class="d-block text-left text-dark">
        <p>Please communicate and acknowledge special instructions to the customer: </p><span class="text-danger mt-3">{{ configuration.reservations.special_instructions_text }}</span>
      </div>
      <b-btn class="mt-3" variant="outline-danger" block @click="closeAck()">Customer Acknowledged</b-btn>
    </b-modal>

  </div>
</template>

<script>
  import router from '@/router'
  import rangepro from '@/services/rangepro'
  import dashboard from '@/services/dashboard'
  import formats from '@/services/formats'
  import MaskedInput from 'vue-text-mask'
  import { required } from 'vuelidate/lib/validators'

  export default {
    name: 'Reservations',
    components: {
      MaskedInput
    },
    data () {
      return {
        rangeID: null,
        configuration: null,
        loading: false,
        searched: false,
        search: {
          search_date: null,
          search_time: null,
          search_lanes: 2
        },
        options: {
          session_lengths: [],
          selected_session_length: null,
          default_session_length: null,
          session_types: [],
          selected_session_type: null,
          default_session_type: null
        },
        customSessionLength: {
          name: 'Custom',
          button_text: 'Custom',
          pay_per_use: false,
          unlimited: false,
          minutes_length: 0,
          minutes_setup: 0
        },
        available_times: [],
        search_time_available_time: null,
        search_time_available: false,
        search_result_fields: [
          {
            key: 'time',
            label: 'Time Available',
            sortable: true
          },
          {
            key: 'lanes_available',
            label: 'Lanes Available'
          },
          {
            key: 'id',
            label: 'Action'
          }
        ],
        addReservation: {
          client_name: null,
          phone: '#',
          lanes: null,
          reservation_date: null,
          reservation_time: null,
          paid_in_full: false,
          paid_partial: false,
          paid_partial_amount: '0',
          special_instructions_ack: false
        },
        open_times: [],
        blocked_times: []
      }
    },
    mounted: function () {
      this.rangeID = this.$route.params.id
      this.getInitialRangeConfiguration()
    },
    methods: {
      getInitialRangeConfiguration () {
        this.loading = true
        rangepro.get_secure('store/range/' + this.rangeID, this.handleInitialConfigurationResponse, 'GetStoreRanges')
      },
      searchReservations () {
        this.loading = true
        this.searched = true
        var reservationSearchCriteria = {
          reservation: {
            date: formats.format_date(this.search.search_date),
            time: formats.format_mil_to_std_time(this.search.search_time),
            lanes: this.search.search_lanes,
            minutes_duration: this.options.selected_session_length['minutes_length']
          }
        }
        rangepro.post_secure('store/range/' + this.rangeID + '/reservations/available', reservationSearchCriteria, this.handleSearchResponse, 'SearchRangeReservations', null)
      },
      reservationSave () {
        var reservation = {
          reservations: {
            client_name: this.addReservation.client_name,
            lanes: this.addReservation.lanes,
            phone: this.addReservation.phone,
            paid_in_full: this.addReservation.paid_in_full,
            paid_partial: this.addReservation.paid_partial,
            paid_partial_amount: this.addReservation.paid_partial_amount,
            reservation_date: this.addReservation.reservation_date,
            reservation_time: this.addReservation.reservation_time,
            session_length: this.options.selected_session_length,
            session_type: this.options.selected_session_type,
            special_instructions: this.configuration.reservations.special_instructions,
            special_instructions_ack: this.addReservation.special_instructions_ack
          }
        }
        rangepro.post_secure('store/range/' + this.rangeID + '/reservations', reservation, this.handleReservationSave, 'AddRangeReservation', null)
        this.loading = true
      },
      handleInitialConfigurationResponse (data, success) {
        if (success) {
          this.configuration = data.range
          // Setup add modal options for session_lengths and session_types
          this.options.session_lengths = dashboard.setup_add_options(this.configuration.session_lengths)
          this.options.session_types = dashboard.setup_add_options(this.configuration.session_types)
          this.default_session_length = dashboard.default_add_option(this.configuration.session_lengths)
          this.default_session_type = dashboard.default_add_option(this.configuration.session_types)
          this.options.selected_session_length = this.default_session_length
          this.options.selected_session_type = this.default_session_type
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleSearchResponse (data, success) {
        if (success) {
          this.available_times = data.available_times
          this.open_times = data.open_times
          this.blocked_times = data.blocked_times
          this.search_time_available = data.search_time_available
          this.search_time_available_time = data.search_time_display
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleReservationSave (data, success) {
        if (success) {
          this.loading = false
          this.searched = false
          router.push({name: 'Store Range Dashboard', params: {id: this.rangeID}})
        } else {
          this.loading = false
        }
      },
      selectAvailableTime (time) {
        this.addReservation.reservation_time = time
        this.addReservation.reservation_date = formats.format_date(this.search.search_date)
        this.addReservation.lanes = this.search.search_lanes
        this.$refs.reservationAdd.show()
      },
      checkInstructions () {
        if (!this.configuration.reservations.special_instructions) {
          this.reservationSave()
        } else {
          if (!this.addReservation.special_instructions_ack) {
            this.$refs.ackInstructions.show()
          } else {
            this.reservationSave()
          }
        }
      },
      formatTime (time) {
        return formats.format_mil_to_std_time(time)
      },
      formatDate (date) {
        return formats.format_date(date)
      },
      clearPhone () {
        if (this.addReservation.phone === '(###) ###-####') {
          this.addReservation.phone = ''
        }
      },
      closeAck () {
        this.addReservation.special_instructions_ack = true
        this.reservationSave()
        this.$refs.ackInstructions.hide()
      }
    },
    validations: {
      search: {
        search_date: {
          required
        },
        search_time: {
          required
        },
        search_lanes: {
          required
        }
      },
      addReservation: {
        client_name: {
          required
        },
        phone: {
          required
        },
        paid_partial_amount: {
          required
        }
      }
    }
  }
</script>

<style scoped>

</style>



